import {isVaild, errorTip} from "@/utils/tools";

export const beforeValidate = formRef => {
  let isPass = false
  formRef?.validate(valid => {
    if (valid) isPass = true
  })
  return isPass
}

export const validateLoginPhone = (_, value, callback) => {
  let reg = /^[a-zA-Z]{5,8}$/
  if (!isVaild(value)){
    return callback(new Error(errorTip()))
  } else if(!reg.test(value)) {
    return callback(new Error((errorTip("数据格式不对，正确格式：只有字母组合且长度在5~8之间"))))
  } else callback()
}

export const validateLoginPassword = (_, value, callback) => {
  let reg = /^[a-zA-Z0-9_-]{6,8}$/
  if (!isVaild(value)){
    return callback(new Error(errorTip()))
  } else if(!reg.test(value)) {
    return callback(new Error((errorTip("数据格式不对，正确格式：由数字字母_-组合且长度在6~8之间"))))
  } else callback()
}
